import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet' 
import L from 'leaflet'
import Leaflet from 'leaflet'
import 'leaflet/dist/leaflet.css'
import '../leafletAwesomeMarker/leaflet.awesome-markers.css'
import '../ionicons.css'
// eslint-disable-next-line
import AwesomeMarkers from "../leafletAwesomeMarker/leaflet.awesome-markers" 

function MapNewZealand() {

  L.AwesomeMarkers.Icon.prototype.options.prefix = 'ion';

  var homeMarker = L.AwesomeMarkers.icon({
    // prefix: '',
    // extraClasses: '',
    icon: 'android-home',
    markerColor: 'gray',
    iconColor: '#f3f3f3',
  });
  

  const mapCentre = [-38.5, 170.5]
  const newZealand = [-36.85, 174.76]

  const corner1 = Leaflet.latLng(-90, -180)
  const corner2 = Leaflet.latLng(90, 350)
  // eslint-disable-next-line 
  const bounds = Leaflet.latLngBounds(corner1, corner2)
  // <MapContainer maxBoundsViscosity={1} maxBounds={bounds}>

  return (
    <div className="map-new-zealand">
             <div className="map" id="map" >
              <MapContainer center={mapCentre} zoom={7} scrollWheelZoom={true} zoomControl={false} >
              <TileLayer
              attribution='Tiles &copy; Esri &mdash; Esri, DeLorme, NAVTEQ'
              url='https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png'
              minZoom={7}
              maxZoom={7}
              />
                <Marker position={newZealand} icon={homeMarker}>
                  <Popup closeButton={false} className={'popup'}>
                   Home!
                  </Popup>
                </Marker>
              </MapContainer>
            </div>
    </div>
  )
}

export default MapNewZealand
