import { Routes, Route, Navigate } from 'react-router-dom'

import './App.css';

import Home from './components/Home'
import Header from './components/Header'
import Footer from './components/Footer'
import Contact from './components/Contact'
import Projects from './components/Projects'
import About from './components/About'
import ProjectHouse from './components/ProjectHouse'
import ProjectHasBean from './components/ProjectHasBean'
import ProjectBird from './components/ProjectBird';
import MapWorld from './components/MapWorld';
import MapNewZealand from './components/MapNewZealand';
// import PageNotFound from './components/PageNotFound';

function App() {
  return (
    <>
      <Header />
      <Routes>
        <Route path="/portfolio" element={<Home />}/>
        <Route path="/portfolio/projects" element={<Projects />}/>
        <Route path="/portfolio/houseplanner" element={<ProjectHouse />}/>
        <Route path="/portfolio/hasbean" element={<ProjectHasBean/>}/>
        <Route path="/portfolio/birdperson" element={<ProjectBird />}/>
        <Route path="/portfolio/about" element={<About />}/>
        <Route path="/portfolio/contact" element={<Contact />}/>
        <Route path="/portfolio/map" element={<MapWorld />}/>
        <Route path="/portfolio/mapnz" element={<MapNewZealand/>}/>
        <Route path="*" element={<Navigate to="/portfolio" />}/>
      </Routes>
      <Footer />
    </>
  );
}

export default App;
