import React from 'react';

export function FadeInSectionB(props) {
  const [isVisibleB, setVisibleB] = React.useState(false);
  const domRef = React.useRef();
  React.useEffect(() => {
    const observer = new IntersectionObserver(entries => {
      entries.forEach(entry => setVisibleB(entry.isIntersecting));
    });
    observer.observe(domRef.current);
  }, []);
  return (
    <div
      className={`fade-in-section-b ${isVisibleB ? 'is-visible' : ''}`}
      ref={domRef}
    >
      {props.children}
    </div>
  );
}