import { Link } from 'react-router-dom'

import { FadeInSectionB } from './FadeInSectionB'

import './Footer.css'

function Footer() {
  const handleClick = () => {
    window.scrollTo({ top: 0, left: 0, behavior: 'smooth' })
  }

  window.onscroll = function() {scrollFunction()};

  function scrollFunction() {
    if (document.body.scrollTop > 700 || document.documentElement.scrollTop > 700) {
      document.getElementById("top-button").className = "to-top";
    } else {
      document.getElementById("top-button").className = "to-top-hidden";
    }
  }

  return (
    <div className="footer">
      <div className="container">
        <div className="container-app">
          <div className="Fade-in">

            <FadeInSectionB>
              <div className="footer-flex">
                <div className="footer-item">
                  <Link to="https://github.com/eq-hong" target="_blank">
                    <i className="fa fa-github icons" aria-hidden="true"></i>
                  </Link>
                </div>
                <div className="footer-item">
                  <Link to="https://www.linkedin.com/in/eq-hong/" target="_blank">
                    <i class="fa fa-linkedin-square" aria-hidden="true"></i>
                  </Link>
                </div>
              </div>
            </FadeInSectionB>


            <div id="top-button" className="to-top-hidden" onClick={handleClick}>
              <div className='nav-link'>to top</div>
            </div>


          </div>
        </div>
      </div>
    </div>
  )
}

export default Footer
